.project-sub-des{
padding: 30px;
/* width: 50%; */
/* border: 1px solid black; */
}
.project-sub-des>div{
padding: 5px 0;
}
.project-sub-des>div:nth-child(1){
    color: #F9004D;
}
.project-sub-des>div:nth-child(2){
    /* font-size: 20px; */
    font-weight: 600;
    color: #3D3E41;
}


.tech-stacks{
    flex-wrap: wrap;
    margin-bottom: 10px;
}
.tech-stacks>div{
    transform: skew(-15deg);
    padding: 8px 8px;
    font-size: 14px;
    margin: 8px 8px;
    color: #F9004D;
    border-radius: 5px;
    border: .2px solid #878E99;
    background:  linear-gradient(145deg, #e2e8ec, #ffffff);
    /* box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff; */
}
.tech-stacks>div:hover{
    border: .2px solid #F9004D;
    cursor: pointer;
    transition: color .3s ease-in-out;
     box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;

}
.project-card-btn>button{
    padding:10px  10px;
    font-weight: 500;
    color:#F9004D;
    background:  linear-gradient(145deg, #e2e8ec, #ffffff);
    box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;

}
.project-card-btn>button:hover{
    color:white;
    background-color: #FF014F;
    transition: background-color .2s ease-in ;
    /* transition: color .2s ease-in ; */
}
.tab-btn{
    width: 50%;
}

@media screen and (max-width:460px) {
    .project-card-btn{
        flex-wrap: wrap;
       
    }

    .project-card-btn>button {
        width: 100%;
        margin: 8px;
    }
    #tablist-con{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }
    .tab-btn{
        width: 100%;
    }
    .project-tech-stack>div{
        font-size: 10px;
        margin: 4px;
        padding: 4px;
    }
    #project-dis{
        font-size: 12px;
        line-height: 1.5;
        margin: 10px 0;

    }
    #project-ttl{
        font-size: 20px;
        margin:0;
    }
}